<style lang="less" scoped>
.item{
  background: #fff;
  margin-top:20px;
  padding: 20px 30px;
  .item-title{
    line-height: 50px;
  }
  .item-content{
    .text{
      text-align: center;
      margin:10px 0;
      span{
        display: inline-block;
      }
      .time{
        color: #555;
        font-size: 14px;
        margin-left: 10px;
      }
    }
  }
}
</style>
<template>
<div class="article">
  <div class="breadcrumb">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="item">
      <div class="item-content" v-for="item in articleList">
        <div class="text"><span class="name"><router-link :to="{path:'/detail',query:Object.assign({last:'article',lastName:''},item)}">{{item.name}}</router-link></span> <span class="time">{{item.time}}</span></div>
        <div class="article" v-html="item.content" >

        </div>
      </div>
    </div>
  </div>
</div>
</template>
<script>
export default {
  name: 'article',
  data () {
    return {
      articleList:[
        {
          name:"",
          time:"2017-03-20",
          content:'<p>3</p><p>3X3</p><p>XX</p><p>X23</p><p>XX</p><p></p><p> XXX</p><p></p><p>XXX</p><p>XX</p><p></p><p></p><p>X24</p><p></p><p></p>'
        }
      ]
    }
  }
}
</script>



