<style lang="less" scoped>
.item{
  background: #fff;
  padding: 20px 30px;
  margin-bottom: 20px;
  .item-content{
    .item-left{
      float: left;
      width: 35%;
      img{
        width:100%;
        cursor: pointer;
      }
    }
    .item-right{
      float: right;
      width: 62%;
    }
  }
}
.row{
  margin-top:10px;
  width: 100%;
  .row-left{
    float: left;
    width: 15%;
    text-align: right;
    color: #555;
  }
  .row-right{
    float: right;
    width: 82%;
  }
}
</style>
<template>
<div class="trailer">
  <div class="breadcrumb">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
  </div>
  <div class="item" v-for="item in trailerList">
    <div class="item-content clearfix">
      <div class="item-left"><img :src="item.img" alt="" @click="showVideo(item.url)"/></div>
      <div class="item-right">
        <div class="row clearfix">
          <div class="row-left">:</div>
          <div class="row-right"><router-link :to="{path:'/detail',query:Object.assign({last:'trailer',lastName:''},item)}">{{item.name}}</router-link></div>
        </div>
        <div class="row clearfix">
          <div class="row-left">:</div>
          <div class="row-right">{{item.time}}</div>
        </div>
        <div class="row clearfix">
          <div class="row-left">:</div>
          <div class="row-right" v-html="item.intro"></div>
        </div>
      </div>
    </div>
  </div>
  <el-dialog title="" v-model="dialogVisible" size="">
    <div v-html="url"></div>
  </el-dialog>
</div>
</template>
<script>
export default {
  name: 'trailer',
  data () {
    return {
      trailerList:[{
        name:"",
        time:"2017-03-17(/)",
        img:"src/assets/images/home-p1.webp",
        url:'<embed height="415" width="544" quality="high" allowfullscreen="true" type="application/x-shockwave-flash" src="//static.hdslb.com/miniloader.swf" flashvars="aid=8382491&page=1" pluginspage="//www.adobe.com/shockwave/download/download.cgi?P1_Prod_Version=ShockwaveFlash"></embed>',
        intro:"<p> Emma Watson  Luke Evans  Kevin Kline  Dan Stevens </p><p> Emma Thompson  Ewan McGregor  Ian McKellen </p>"
      },
      {
        name:" La La Land",
        time:"2017-02-14()/2016-08-31()/2016-12-25()",
        img:"src/assets/images/lalaland.webp",
        url:'<embed height="415" width="544" quality="high" allowfullscreen="true" type="application/x-shockwave-flash" src="//static.hdslb.com/miniloader.swf" flashvars="aid=6966258&page=1" pluginspage="//www.adobe.com/shockwave/download/download.cgi?P1_Prod_Version=ShockwaveFlash"></embed>',
        intro:"<p> Emma Stone  Ryan Gosling </p><p></p>"
      }]
      ,
      dialogVisible:false,
      url:""
    }
  },
  methods:{
    showVideo(url){
      this.dialogVisible=true;
      this.url=url;
    }
  },
  computed:{

  }
}
</script>



